'use client';

import { useState } from 'react';
import {
  Clock,
  Truck,
  Package,
  DollarSign,
  Plus,
  Search,
  FileText,
  Check,
  X,
  Eye,
  AlertTriangle,
  ChevronLeft,
  ChevronRight,
} from 'lucide-react';
import {
  purchaseOrders,
  suppliers,
  products,
  getProductById,
  formatCurrencyPrecise,
  getLowStockItems,
} from '@/data/mockData';

export default function PurchaseOrdersPage() {
  const [statusFilter, setStatusFilter] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showReceiveModal, setShowReceiveModal] = useState(false);

  const filteredPOs = purchaseOrders.filter((po) => {
    if (statusFilter && po.status !== statusFilter) return false;
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      return (
        po.poNumber.toLowerCase().includes(query) ||
        po.supplier.name.toLowerCase().includes(query)
      );
    }
    return true;
  });

  const pendingCount = purchaseOrders.filter((po) => po.status === 'draft' || po.status === 'sent').length;
  const inTransitCount = purchaseOrders.filter((po) => po.status === 'sent').length;
  const readyCount = purchaseOrders.filter((po) => po.status === 'partially_received').length;
  const totalOpenValue = purchaseOrders.reduce((sum, po) => {
    if (po.status !== 'received') return sum + po.subtotalUSD;
    return sum;
  }, 0);

  const lowStockItems = getLowStockItems();

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'draft':
        return (
          <span className="px-2 py-1 bg-slate-500/10 text-slate-400 text-xs rounded-full">Draft</span>
        );
      case 'sent':
        return (
          <span className="px-2 py-1 bg-amber-500/10 text-amber-400 text-xs rounded-full">Pending</span>
        );
      case 'partially_received':
        return (
          <span className="px-2 py-1 bg-blue-500/10 text-blue-400 text-xs rounded-full">Partial</span>
        );
      case 'received':
        return (
          <span className="px-2 py-1 bg-emerald-500/10 text-emerald-400 text-xs rounded-full">Received</span>
        );
      default:
        return (
          <span className="px-2 py-1 bg-slate-500/10 text-slate-400 text-xs rounded-full">{status}</span>
        );
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Purchase Orders</h1>
          <p className="text-sm text-slate-400">Manage supplier orders and inventory replenishment</p>
        </div>
        <div className="flex items-center gap-3">
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <FileText className="w-4 h-4" />
            Export
          </button>
          <button
            onClick={() => setShowCreateModal(true)}
            className="px-4 py-2 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <Plus className="w-4 h-4" />
            New PO
          </button>
        </div>
      </div>

      {/* Stats Row */}
      <div className="grid grid-cols-6 gap-4">
        <div className="bg-slate-800/50 backdrop-blur border border-amber-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-amber-500/20 flex items-center justify-center">
              <Clock className="w-5 h-5 text-amber-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-amber-400">{pendingCount}</div>
              <div className="text-xs text-slate-400">Pending</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-blue-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-blue-500/20 flex items-center justify-center">
              <Truck className="w-5 h-5 text-blue-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-400">{inTransitCount}</div>
              <div className="text-xs text-slate-400">In Transit</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-purple-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-purple-500/20 flex items-center justify-center">
              <Package className="w-5 h-5 text-purple-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-purple-400">{readyCount}</div>
              <div className="text-xs text-slate-400">Ready to Receive</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-slate-700/50 flex items-center justify-center">
              <DollarSign className="w-5 h-5 text-slate-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-white">{formatCurrencyPrecise(totalOpenValue)}</div>
              <div className="text-xs text-slate-400">Open PO Value</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-cyan-500/20 flex items-center justify-center">
              <span className="text-cyan-400 font-bold text-sm">CAD</span>
            </div>
            <div>
              <div className="text-2xl font-bold text-cyan-400">$8,200</div>
              <div className="text-xs text-slate-400">CAD Committed</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-emerald-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-emerald-500/20 flex items-center justify-center">
              <Check className="w-5 h-5 text-emerald-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-emerald-400">12</div>
              <div className="text-xs text-slate-400">Received MTD</div>
            </div>
          </div>
        </div>
      </div>

      {/* Filters & Search */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
        <div className="flex items-center gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
            <input
              type="text"
              placeholder="Search POs..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full bg-slate-800/50 border border-slate-700 rounded-lg pl-10 pr-4 py-2.5 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500/50"
            />
          </div>
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="">All Statuses</option>
            <option value="draft">Draft</option>
            <option value="sent">Pending</option>
            <option value="partially_received">Partial</option>
            <option value="received">Received</option>
          </select>
          <select className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50">
            <option>All Suppliers</option>
            {suppliers.map((s) => (
              <option key={s.id} value={s.id}>{s.name}</option>
            ))}
          </select>
          <select className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50">
            <option>All Currencies</option>
            <option value="USD">USD</option>
            <option value="CAD">CAD</option>
          </select>
        </div>
      </div>

      {/* PO Table */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-800/50 border-b border-slate-700/50">
              <tr className="text-left text-xs text-slate-400 uppercase tracking-wider">
                <th className="px-4 py-3 font-medium">PO Number</th>
                <th className="px-4 py-3 font-medium">Supplier</th>
                <th className="px-4 py-3 font-medium">Items</th>
                <th className="px-4 py-3 font-medium text-right">Total (Original)</th>
                <th className="px-4 py-3 font-medium text-right">Total (USD)</th>
                <th className="px-4 py-3 font-medium">Expected</th>
                <th className="px-4 py-3 font-medium text-center">Status</th>
                <th className="px-4 py-3 font-medium text-center">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-700/50">
              {filteredPOs.map((po) => {
                const itemCount = po.items.length;
                const totalUnits = po.items.reduce((sum, item) => sum + item.quantity, 0);
                const receivedUnits = po.items.reduce((sum, item) => sum + item.quantityReceived, 0);

                return (
                  <tr key={po.id} className="hover:bg-slate-700/30 transition-colors">
                    <td className="px-4 py-4">
                      <div className="font-mono text-sm font-medium text-white">{po.poNumber}</div>
                      <div className="text-xs text-slate-500">{new Date(po.createdAt).toLocaleDateString()}</div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="text-sm text-white">{po.supplier.name}</div>
                      <div className="text-xs text-slate-500">{po.supplier.email}</div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="text-sm text-white">{itemCount} items</div>
                      <div className="text-xs text-slate-500">
                        {receivedUnits > 0 ? `${receivedUnits}/${totalUnits} received` : `${totalUnits} units`}
                      </div>
                    </td>
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-white">
                        {po.currency === 'CAD' ? 'C$' : '$'}{po.subtotal.toLocaleString()}
                      </div>
                      {po.currency === 'CAD' && (
                        <div className="text-xs text-slate-500">@ {po.exchangeRate} rate</div>
                      )}
                    </td>
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm font-medium text-white">{formatCurrencyPrecise(po.subtotalUSD)}</div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="text-sm text-white">{po.expectedDate}</div>
                    </td>
                    <td className="px-4 py-4 text-center">
                      {getStatusBadge(po.status)}
                    </td>
                    <td className="px-4 py-4">
                      <div className="flex items-center justify-center gap-1">
                        <button className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors" title="View">
                          <Eye className="w-4 h-4" />
                        </button>
                        {(po.status === 'sent' || po.status === 'partially_received') && (
                          <button
                            onClick={() => setShowReceiveModal(true)}
                            className="px-3 py-1.5 bg-emerald-600 hover:bg-emerald-500 text-white text-xs rounded-lg"
                          >
                            Receive
                          </button>
                        )}
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="px-5 py-4 border-t border-slate-700/50 flex items-center justify-between">
          <div className="text-sm text-slate-400">
            Showing <span className="text-white">1-{filteredPOs.length}</span> of <span className="text-white">{purchaseOrders.length}</span> purchase orders
          </div>
          <div className="flex items-center gap-2">
            <button className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors disabled:opacity-50" disabled>
              <ChevronLeft className="w-4 h-4" />
            </button>
            <button className="px-3 py-1.5 bg-emerald-500/20 text-emerald-400 border border-emerald-500/30 rounded-lg">1</button>
            <button className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors">
              <ChevronRight className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>

      {/* Low Stock Suggestions */}
      {lowStockItems.length > 0 && (
        <div className="bg-slate-800/50 backdrop-blur border border-amber-500/30 rounded-xl overflow-hidden">
          <div className="px-5 py-4 border-b border-slate-700/50 flex items-center gap-3">
            <AlertTriangle className="w-5 h-5 text-amber-400" />
            <div>
              <h2 className="font-semibold text-white">Low Stock Suggestions</h2>
              <p className="text-xs text-slate-400">Products below reorder point - consider adding to next PO</p>
            </div>
          </div>
          <div className="p-4 grid grid-cols-4 gap-3">
            {lowStockItems.slice(0, 4).map((item) => (
              <div key={item.product.id} className="bg-slate-800/50 border border-slate-700/50 rounded-lg p-3">
                <div className="flex items-center gap-2 mb-2">
                  <Package className="w-4 h-4 text-amber-400" />
                  <span className="text-sm font-medium text-white truncate">{item.product.name}</span>
                </div>
                <div className="text-xs text-slate-400 mb-1">SKU: {item.product.sku}</div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-slate-400">Stock:</span>
                  <span className="text-amber-400 font-medium">{item.currentQty} / {item.reorderPoint}</span>
                </div>
                <button className="mt-2 w-full px-2 py-1.5 bg-amber-500/20 text-amber-400 text-xs rounded hover:bg-amber-500/30">
                  Add to PO
                </button>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Create PO Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black/70 backdrop-blur-sm" onClick={() => setShowCreateModal(false)} />
          <div className="relative bg-slate-900 border border-slate-700 rounded-2xl w-full max-w-2xl max-h-[90vh] overflow-auto m-4">
            <div className="p-6 border-b border-slate-700 flex items-center justify-between sticky top-0 bg-slate-900 z-10">
              <h2 className="text-xl font-bold text-white">Create Purchase Order</h2>
              <button onClick={() => setShowCreateModal(false)} className="p-2 text-slate-400 hover:text-white hover:bg-slate-800 rounded-lg">
                <X className="w-5 h-5" />
              </button>
            </div>
            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm text-slate-400 mb-2">Supplier</label>
                <select className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white focus:outline-none focus:border-emerald-500">
                  {suppliers.map((s) => (
                    <option key={s.id} value={s.id}>{s.name} ({s.currency})</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm text-slate-400 mb-2">Add Products</label>
                <div className="space-y-2">
                  {products.filter(p => p.category === 'nutrient').slice(0, 3).map((product) => (
                    <div key={product.id} className="flex items-center gap-4 p-3 bg-slate-800/50 rounded-xl border border-slate-700">
                      <input type="checkbox" className="rounded bg-slate-700 border-slate-600 text-emerald-500" />
                      <div className="flex-1">
                        <div className="text-white">{product.name}</div>
                        <div className="text-xs text-slate-400">SKU: {product.sku}</div>
                      </div>
                      <input
                        type="number"
                        placeholder="Qty"
                        className="w-24 bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white text-center text-sm"
                      />
                    </div>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm text-slate-400 mb-2">Expected Delivery Date</label>
                <input
                  type="date"
                  className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white focus:outline-none focus:border-emerald-500"
                />
              </div>

              <div>
                <label className="block text-sm text-slate-400 mb-2">Notes</label>
                <textarea
                  rows={3}
                  placeholder="Optional notes for supplier..."
                  className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500"
                />
              </div>

              <div className="flex gap-3 pt-4">
                <button onClick={() => setShowCreateModal(false)} className="flex-1 px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-xl">
                  Cancel
                </button>
                <button onClick={() => setShowCreateModal(false)} className="flex-1 px-4 py-3 bg-emerald-600 hover:bg-emerald-500 text-white rounded-xl font-medium">
                  Create PO
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Receive PO Modal */}
      {showReceiveModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black/70 backdrop-blur-sm" onClick={() => setShowReceiveModal(false)} />
          <div className="relative bg-slate-900 border border-slate-700 rounded-2xl w-full max-w-lg m-4">
            <div className="p-6 border-b border-slate-700">
              <h2 className="text-xl font-bold text-white">Receive Items</h2>
              <p className="text-sm text-slate-400">PO-2024-089</p>
            </div>
            <div className="p-6 space-y-4">
              <div className="space-y-3">
                <div className="flex items-center justify-between p-3 bg-slate-800/50 rounded-lg">
                  <div>
                    <div className="text-white">1L Micro 5-0-1</div>
                    <div className="text-xs text-slate-400">Expected: 500 units</div>
                  </div>
                  <input
                    type="number"
                    defaultValue={500}
                    className="w-24 bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white text-center"
                  />
                </div>
                <div className="flex items-center justify-between p-3 bg-slate-800/50 rounded-lg">
                  <div>
                    <div className="text-white">1L Bud Booster 0-1-3</div>
                    <div className="text-xs text-slate-400">Expected: 200 units</div>
                  </div>
                  <input
                    type="number"
                    defaultValue={200}
                    className="w-24 bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white text-center"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm text-slate-400 mb-2">Receive Location</label>
                <select className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white focus:outline-none focus:border-emerald-500">
                  <option>Cronk Warehouse</option>
                </select>
              </div>

              <div className="flex gap-3 pt-4">
                <button onClick={() => setShowReceiveModal(false)} className="flex-1 px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-xl">
                  Cancel
                </button>
                <button onClick={() => setShowReceiveModal(false)} className="flex-1 px-4 py-3 bg-emerald-600 hover:bg-emerald-500 text-white rounded-xl font-medium flex items-center justify-center gap-2">
                  <Check className="w-4 h-4" />
                  Receive Items
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
